import React from 'react';
import '../css/Card.css';

function Card({src, title , desc}) {
    return (
        <div className="card">
            <img src={src} alt=""/>
            <div className="card_info">
                <h2>{title}</h2>
                <h3>{desc}</h3>
            </div>
        </div>
    )
}

export default Card
