import React from 'react'
import '../css/BlackCard.css'

function BlackCard({src,title,desc}) {
    return (
    <div className="blackCard">
        <img src={src} alt=""/>
        <div className="blackCard_info">
            <h2>{title}</h2>
            <h3>{desc}</h3>
        </div>
    </div>
    )
}

export default BlackCard
