import React from 'react';
import '../css/Home.css';
import Banner from './Banner.js';
import Card from './Card.js';
import BlackCard from './BlackCard.js';
import 'react-bootstrap';
import Footer from './Footer.js';

function Home() {

    const black_card = 'black_card';

    return (
        <div className="home">
            <Banner/>

            <div className="home_container">
                <h2 className="subject">Live anywhere</h2>
                <div className="home_section">
                    <Card
                    src="https://a0.muscache.com/im/pictures/fdb46962-10c1-45fc-a228-d0b055411448.jpg?im_w=720"
                    title="Entire homes"
                    />
                    <Card
                    src="https://a0.muscache.com/im/pictures/15159c9c-9cf1-400e-b809-4e13f286fa38.jpg?im_w=720"
                    title="Unique stays"
                    />
                    <Card
                    src="https://cdn.pixabay.com/photo/2018/09/24/10/07/japan-3699497_960_720.jpg"
                    title="Oriental house"
                    />
                    <Card
                    src="https://cdn.pixabay.com/photo/2018/04/10/16/05/people-3307913_960_720.jpg"
                    title="Pets welcome"
                    />
                </div>
            </div>
            
            <div className={`home_container ${black_card}`}>
                <h2 className="subject">Experience the world</h2>
                <h3 className="sub-subject">Unique activities with local
                experts- in person or online.</h3>
                <div className="home_section">
                    <BlackCard
                    src="https://cdn.pixabay.com/photo/2020/05/17/22/49/online-meeting-5183791_960_720.jpg"
                    title="Online Experiences"
                    desc="Travel the world without leaving home"
                    />
                    <BlackCard
                    src="https://cdn.pixabay.com/photo/2016/11/19/12/58/adventure-1839147_960_720.jpg"
                    title="Experiences"
                    desc="things to do wherever you are."
                    />
                    <BlackCard
                    src="https://cdn.pixabay.com/photo/2017/07/31/11/23/people-2557410_960_720.jpg"
                    title="Adventures"
                    desc="Multi-day trips with meals and stays"
                    />
                </div>
            </div>
            <Footer/>
        </div>
    )
}

export default Home
